@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
body{
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    font-family: 'Fira Sans', sans-serif;
}


@media (min-width: 858px) {
    html {
        font-size: 16px;
    }
}

@media (min-width: 780px) {
    html {
        font-size: 15px;
    }
}

@media (min-width: 702px) {
    html {
        font-size: 14px;
    }
}

@media (min-width: 624px) {
    html {
        font-size: 13px;
    }
}

@media (max-width: 623px) {
    html {
        font-size: 12px;
    }
}

.landing_div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: #1E1E24;
    color: white;
}
.landing_text{
    -webkit-justify-content: center;
            justify-content: center;
}
.landing_text h1{
    font-family: 'Fira Sans', sans-serif;
    font-size: 4rem;
    text-align: center;
}

.landing_text h2{
    font-family: 'Fira Sans', sans-serif;
    font-size: 2rem;
    text-align: center;
}
@media (min-width: 1024px) {
    .landing_text h1 {
        font-size: 8rem;
    }
    .landing_text h2 {
        font-size: 4rem;
    }
}
.about_div{
